import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import ValidasiMasterKondisiPembelian from "../../../Validasi/ValidasiMasterKondisiPembelian.jsx";
import Loading from 'react-fullscreen-loading';
import { createNumberMask } from "redux-form-input-masks";

const maptostate = (state) => {
  if (state.datamaster.ShowModalKondisiPembelian !== null) {
    return {
      initialValues: {
        id: state.datamaster.ShowModalKondisiPembelian.id,
        kondisi_barang_lama: state.datamaster.ShowModalKondisiPembelian.kondisi_barang,
        kondisi_barang: state.datamaster.ShowModalKondisiPembelian.kondisi_barang,
        potongan_harga: state.datamaster.ShowModalKondisiPembelian.potongan_harga,
      },
    };
  }
};
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class FormModalKondisiPembelian extends Component {
  state = {
    aktif: false,
  };
  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("kondisi_barang").focus()
        : document.getElementById("kondisi_barang").focus();
    }, 100);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <input type="hidden" name="kondisi_barang_lama" />
        {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <Field
          tabIndex="1"
          id="kondisi_barang"
          name="kondisi_barang"
          component={ReanderField}
          type="text"
          label=" Kondisi Pembelian"
          placeholder="Masukan  Kondisi Pembelian"
        />
        <Field
          tabIndex="2"
          id="potongan_harga"
          name="potongan_harga"
          component={ReanderField}
          type="text"
          {...currencyMask}
          label="Potongan Harga"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Potongan Harga"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormModalKondisiPembelian = reduxForm({
  form: "ModalKondisiPembelian",
  enableReinitialize: true,
  validate: ValidasiMasterKondisiPembelian,
})(FormModalKondisiPembelian);
export default connect(maptostate, null)(FormModalKondisiPembelian);
