import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalKondisiPembelian,
  showModal,
  getKondisiPembelian,
  hideModal,
  getMasterToko,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import {
  deleteData,
  deleteParameterHarga,
  postData,
  postParameterHarga,
  putData,
  putParameterHarga,
} from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalKondisiPembelian from "./FormModalKondisiPembelian.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataKondisiPembelian: state.datamaster.getKondisiPembelian,
    hideModal: state.datamaster.modalDialog,
    getMasterToko: state.datamaster.getMasterToko,
  };
};
const hapusDataKondisiPembelian = (params, dispatch, listToko) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Kondisi Pembelian " +
      params.kondisi_barang +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  })
    .then((result) => {
      if (result.isConfirmed) {
        listToko.map((data) => {
          deleteParameterHarga(
            data.portal +
              "/api/v1/parabeli/delete/by-kode-kondisi/" +
              params._id
          ).then(() => {
            NotifSucces("Data Berhasil Di Hapus");
          });
          return true;
        });
        deleteData("parabeli/delete/by-kode-kondisi/" + params._id).then(() => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getKondisiPembelian())
          );
        });
      }
    })
    .catch((err) => {
      ToastNotification("error", err.response.data);
    });
};
class MasterKondisiPembelian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kondisi_barang",
          text: "Kode Kondisi Pembelian",
          sort: true,
        },
        {
          dataField: "potongan",
          text: "Potongan Harga",
          formatter: (cell) => {
            // console.log(cell);
            return cell === undefined ? 0 : cell.toLocaleString("kr-KO");
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kondisi_barang: row.kondisi_barang,
              potongan_harga: row.potongan,
            };

            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditKondisiPembelian(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataKondisiPembelian(
                        row,
                        this.props.dispatch,
                        this.props.getMasterToko
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getKondisiPembelian());
    this.props.dispatch(getMasterToko());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_edit = {
      kondisi_barang: data.kondisi_barang,
      potongan: data.potongan_harga,
      persentase: 1,
    };
    let data_post = {
      kondisi_barang: data.kondisi_barang,
      potongan: data.potongan_harga || 0,
      persentase: 1,
    };
    let listToko = this.props.getMasterToko;
    if (this.state.isEdit) {
      listToko.map((data) => {
        putParameterHarga(
          data.portal + "/api/v1/parabeli/edit/by-kode-kondisi/" + data.id,
          data_edit
        )
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getKondisiPembelian());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.setState({ isLoading: false });
            this.props.dispatch(reset("ModalKondisiPembelian"));
          })
          .catch((error) => {
            this.setState({ isLoading: false });
            ToastNotification(
              "info",
              error.response === undefined
                ? "Terjadi Kesalahan Saat Megirim Data "
                : error.response.data
            );
          });
        return true;
      });
      putData("parabeli/edit/by-kode-kondisi/" + data.id, data_edit)
        .then(() => {
          this.props.dispatch(hideModal());
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .then(() => {
          this.setState({ isLoading: false });
          this.props.dispatch(reset("ModalKondisiPembelian"));
        })
        .catch((error) => {
          this.setState({ isLoading: false });
          ToastNotification(
            "info",
            error.response === undefined
              ? "Terjadi Kesalahan Saat Megirim Data "
              : error.response.data
          );
        });
    } else {
      listToko.map((data) => {
        postParameterHarga(data.portal + "/api/v1/parabeli/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            this.props.dispatch(reset("ModalKondisiPembelian"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "parabeli/reactive/by-kode-kondisi/",
              // data.kondisi_barang,
              data_post,
              this.props.dispatch,
              getKondisiPembelian(),
              hideModal(),
              "ModalKondisiPembelian"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
        return true;
      });
      postData("parabeli/add", data_post)
        .then(() => {
          this.props.dispatch(hideModal());
        })
        .then(() => {
          ToastNotification("success", "Data Berhasil Disimpan");
        })
        .then(() => {
          this.props.dispatch(getKondisiPembelian());
        })
        .then(() => {
          this.props.dispatch(reset("ModalKondisiPembelian"));
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .catch((error) =>
          ErrorHandling(
            error,
            "parabeli/reactive/by-kode-kondisi/",
            // data.kondisi_barang,
            data_post,
            this.props.dispatch,
            getKondisiPembelian(),
            hideModal(),
            "ModalKondisiPembelian"
          ).then(() => {
            this.setState({ isLoading: false });
          })
        );
    }
  }
  formEditKondisiPembelian(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiPembelian(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalKondisiPembelian() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiPembelian(false));
    this.setState({
      isEdit: false,
    });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kondisi Pembelian</li>
        </ol>
        <h1 className="page-header">Master Kondisi Pembelian </h1>
        <Panel>
          <PanelHeader>Master Kondisi Pembelian</PanelHeader>
          <br />

          {/* Master Kondisi Pembelian */}
          <div className="container">
            <Tabel
              keyField="kondisi_barang"
              tambahData={true}
              handleClick={() => this.ShowModalKondisiPembelian()}
              exportCsv={true}
              data={this.props.DataKondisiPembelian}
              columns={this.state.columns}
              empty={this.props.DataKondisiPembelian}
              textEmpty="Data Kondisi Pembelian Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Data Kondisi Pembelian"
                : "Tambah Data Kondisi Pembelian"
            }
            content={
              <FormModalKondisiPembelian
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Kondisi Pembelian  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterKondisiPembelian);
